body {
  background-image: linear-gradient(to bottom,red , rgb(212, 208, 208),rgb(212, 208, 208));
  background-attachment:fixed;
  background-repeat: no-repeat;
  /* padding-top: 5%;
  padding-left: 4%;
  padding-right: 4%; */
  /* color:white; */
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.carousel-inner img{
  width: 100%;
  height:auto;
  max-height:550px;
}
@media only screen and (min-width : 300px) and (max-width : 768px) and (orientation : landscape) {
  .navbar-header {
        float: none;
    }
    .navbar-left,.navbar-right {
        float: none !important;
    }
   
   
    .navbar-fixed-top {
		top: 0;
		border-width: 0 0 1px;
	}
    
    .navbar-nav {
        float: none!important;
		margin-top: 7.5px;
	}
	.navbar-nav>li {
        float: none;
    }
    .navbar-nav>li>a {
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .collapse.in{
  		display:block !important;
  border:1px solid red !important;
  overflow:scroll;
	}
}
@media (max-width: 992px) {
    .navbar-header {
        float: none;
    }
    .navbar-left,.navbar-right {
        float: none !important;
    }
    
    .navbar-collapse {
        border-top: 1px solid transparent;
        box-shadow: inset 0 1px 0 rgba(255,255,255,0.1);
    }
    .navbar-fixed-top {
		top: 0;
		border-width: 0 0 1px;
	}
    
    .navbar-nav {
        float: none!important;
		margin-top: 7.5px;
	}
	.navbar-nav>li {
        float: none;
    }
    .navbar-nav>li>a {
        padding-top: 10px;
        padding-bottom: 10px;
    }
 
}